@import url(https://fonts.googleapis.com/css?family=Orbitron);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
  background: linear-gradient(to right, #e99467 0%, #ec6748 100%);
}

.calc-wrapper {
  width: 400px;
}

/* When I set flex on the row, every button will show in one line */ 
.row {
  display: flex;
  width: 100%;
}
.Button {
    display: flex;
    height: 4em;
    flex: 1 1;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 1.4em;
    background-color: #e0e1e6;
    color:#888;
    outline: 1px solid #888;
}

.Button:hover {
    background-color: #888;
    color: white;
}

.operator {
    background-color: darkgray;  
    color: #fff;
}

/* Flex-end will make the input show in the end */

.Input {
display: flex;
height: 4em;
flex: 1 1;
background-color: #292929;
outline: 1px solid #888;
color: #fff;
justify-content: flex-end;
padding: 0.5em;
font-weight: bold;
font-size: 1.4em;
align-items: center;
font-family: 'Orbitron';
}
.ClearButton {
    display: flex;
    flex: 1 1;
    height: 4em;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 1.rem;
    outline: 1px solid #888;
    height: 4em;
    background-color: #c5c3cd;
}

.ClearButton:hover {
    color: white;
}
